let changelook = ['所有人可见', '尽自己可见'];
import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
import uploadImages from '../../components/common/upload-images.vue';
import uniPopup from '../../components/uni-popup/uni-popup.vue';
export default {
	components: {
		uniNavBar,
		uploadImages,
		uniPopup
	},
	data() {
		return {
			yinsi: '所有人可见',
			text: '',
			imageList: [],
			showpopup: true,
			isget: false
		};
	},
	onBackPress() {
		//如果有值
		if(!this.text&&this.imageList.length<1){return;};
		if (!this.isget) {
			this.baocun();
			return true; //不返回
		}
	},
	methods: {
		//保存
		baocun(){
			uni.showModal({
				content: '是否要保存为草稿',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if (res.confirm) {
						console.log('保存');
					} else {
						console.log('不保存');
					}
					this.isget = true;
					uni.navigateBack({
						delta: 1
					});
				}
			});
		},
		hidePopup() {
			this.showpopup = false;
		},
		upload(arr) {
			this.imageList = arr;
		},
		//返回
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 发布
		submit() {
			console.log('发布');
		},
		// 隐私
		changelook() {
			uni.showActionSheet({
				itemList: changelook,
				success: res => {
					this.yinsi = changelook[res.tapIndex];
				}
			});
		}
	}
};
